@import '../../pages/styles/main';

html,
body {
  height: 100%;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
}

.main-content {
  flex-grow: 1;
}

.footer {
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 32px 16px;
  border-top: 1px solid $color-elements;

  min-height: 90px;
  flex-shrink: 0;

  @include onTablet {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0;
    padding: 32px;
  }

  &--logo {
    @include logo;
  }

  &__navigation {
    &--list {
      display: flex;
      flex-direction: column;
      gap: 16px;

      @include onTablet {
        @include listNav;
      }
    }

    &--item {
      &--link {
        @include linkNav;
      }
    }
  }

  &__back-to-top {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 16px;
    color: $color-secondary;
    text-decoration: none;
    white-space: nowrap;

    &--text {
      @extend %smallText;
    }

    &--icon {
      @extend %smallIcon;
    }

    &:hover {
      color: $color-primary;
    }
  }
}